import PowerSettingsNewRoundedIcon from '@material-ui/icons/PowerSettingsNewRounded';
import React from 'react';
import styles from './style.module.scss';
import { Props } from './type';

const ICON_SIZE = 24;

const PowerButton: React.SFC<Props> = ({ onClick, powerOn = true }) => {
  const handleOnClick = () => {
    onClick(!powerOn);
  };

  return (
    <div className={powerOn ? styles.container : styles.containerOff}>
      <div className={powerOn ? styles.iconWrapper : styles.iconWrapperOff} onClick={handleOnClick}>
        <PowerSettingsNewRoundedIcon
          htmlColor={powerOn ? styles.colorOn : styles.colorOff}
          style={{ height: ICON_SIZE, width: ICON_SIZE }}
        />
      </div>
    </div>
  );
};

export default PowerButton;
